.form {
    height: 50px;
    display: flex;
    align-items: center;
    margin: 0 20px;
}

.form input,
.form button {
    margin: 5px;
}

.li_item {
    padding: 5px;
}

.li_item button {
    margin: 0 5px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    padding: 1px;
    border: none;
}

button {
    cursor: pointer;
}